.main-layout {
	display: grid;
	grid-template-columns: 10px 1fr 10px;

	> * {
		grid-column: 2;
	}

	> *.full {
		grid-column: 1 / -1;
	}

	@include themify('background-color', 'bg');
	@include themify('color', 'fc');

	@include for-narrow-layout {
		@include layoutGTC($break-narrow, $layoutPaddingSmall);
	}

	@include for-normal-layout {
		@include layoutGTC($break-normal, $layoutPaddingMedium);
	}

	@include for-wide-layout {
		@include layoutGTC($break-wide, $layoutPaddingLarge);
	}
}

.main-container-layout {
	padding-top: rem(20px);
	min-height: calc(100vh - rem(90px));
	display: grid;
}
